import React, { useEffect, useRef } from "react";
import "./Project.css";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import MyCanvas from "./MyCanvas";
gsap.registerPlugin(ScrollTrigger);

const Project = () => {
  const p = useRef(null);
  let x = 0;
  const h = useRef(null);
  const subh = useRef(null);
  const jumpers = useRef(new Array());
  const caroSection = useRef(new Array());
  const caro = useRef(null);
  let text =
    "Hey!!! welcome to my project section, here you will find some of my personal works , feel free to check out or even distroy  (My MOTO : keep mooving you will get your ways around)".split(
      " "
    );
  const len = text.length;
  useEffect(() => {
    const project = p.current;
    const projectSubHeading = subh.current;
    const projectHeading = h.current;
    gsap.fromTo(
      project,
      { backgroundColor: "white" },
      {
        // x: 100,
        backgroundColor: "#2f2f2f",
        // duration: 2,
        // color: "white",
        // fontFamily: "Bangers",
        scrollTrigger: {
          trigger: project,
          start: "top 80%",
          end: "top 50%",
          // toggleActions: "play reverce none none",
          scrub: true,
          once: true,
          // markers: true,
        },
      }
    );
    gsap.fromTo(
      projectHeading,
      { color: "black" },
      {
        // x: 100,
        // backgroundColor: "black",
        color: "white",
        // fontFamily: "Bangers",
        scrollTrigger: {
          trigger: project,
          start: "top 80%",
          end: "top 50%",
          onLeave: () => {
            projectHeading.classList.add("jump");
          },
          scrub: true,
          once: true,
          // markers: true,
        },
      }
    );
    gsap.to(projectSubHeading, {
      // x: 100,
      // backgroundColor: "black",
      color: "white",
      // fontFamily: "Bangers",
      scrollTrigger: {
        trigger: project,
        start: "top 80%",
        end: "top 50%",
        scrub: true,
        once: true,
        onLeave: () => {
          const loop = setInterval(() => {
            if (x < len) {
              jumpers.current[x].classList.add("jumpers");

              x++;
            } else {
              clearInterval(loop);
            }
          }, 100);
        },
        // markers: true,
      },
    });
  }, []);
  useEffect(() => {
    let tlMain = gsap
      .timeline({
        scrollTrigger: {
          trigger: ".field",
          start: "top top",
          end: "100% bottom",
          scrub: true,
        },
      })
      .to(".track", {
        xPercent: -100,
        ease: "none",
      });
  }, []);
  const data = [
    {
      img: "data:image/png;base64,",
      mobile: [
        "THB-CIRCLE",
        "LANDING PAGE",
        "https://github.com/TRITON1999/TBH-Hero-Section",
      ],
      laptop: "TBH-CIRCLE LANDING PAGE",
    },
    {
      img: "data:image/png;base64,",
      mobile: [
        "BOOM FIST ONLINE",
        "1V1 MULTIPLAYER GAME",
        "https://github.com/TRITON1999/BOOM-FIST",
      ],
      laptop: "BOOM FIST ONLINE 1V1 GAME",
    },
    {
      img: "data:image/png;base64,",
      mobile: [
        "IP-TRACKER",
        "WEB APPLICATION",
        "https://github.com/TRITON1999/ip-tracker/tree/master",
      ],
      laptop: "IP-TRACKER WEB APPLICATION",
    },
  ];
  return (
    <section id="projects" class="projects sec-pad" ref={p}>
      <div class="main">
        <h2 class="">
          <span className="project-heading " ref={h}>
            Projects
          </span>
          <span class="project-sub-heading" ref={subh}>
            {text.map((p, i) => {
              return (
                <span
                  className="j"
                  key={i}
                  ref={(e) => jumpers.current.push(e)}
                >
                  {p + " "}
                </span>
              );
            })}
          </span>
        </h2>
        <div class="field">
          <div class="gsap_practice">
            <div class="track">
              <div class="track-flex">
                <div class="red">
                  <MyCanvas
                    img={data[0].img}
                    mobile={data[0].mobile}
                    laptop={data[0].laptop}
                  />
                </div>
                <div class="white">
                  <MyCanvas
                    img={data[1].img}
                    mobile={data[1].mobile}
                    laptop={data[1].laptop}
                  />
                </div>
                <div class="green">
                  <MyCanvas
                    img={data[2].img}
                    mobile={data[2].mobile}
                    laptop={data[2].laptop}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Project;
